// import React from 'react';
import React, { Component } from 'react'


class Login extends React.Component {
    state = {
        clickd: true
    }
    
    handlesubmit = (e) => {
      e.preventDefault();
      this.setState({clickd: false})
      setTimeout(() => {
          this.props.history.replace('/shopping-cart')
      }, 2000);
    }
    render (){
        return (
            <React.Fragment>
                <h1 className='log-h1' >Log In</h1>
                {this.state.clickd ?<form className='form' onSubmit={this.handlesubmit} >
                    <div className="mb-3">
                        <label htmlFor="exampleInputEmail1" className="form-label">Email address</label>
                        <input type="email" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp"/>
                        <div id="emailHelp" className="form-text text-white">We'll never share your email with anyone else.</div>
                    </div>
                    <div className="mb-3">
                        <label htmlFor="exampleInputPassword1" className="form-label">Password</label>
                        <input type="password" className="form-control" id="exampleInputPassword1"/>
                    </div>
                    {/* <div className="mb-3 form-check">
                        <input type="checkbox" className="form-check-input" id="exampleCheck1"/>
                        <label className="form-check-label" htmlFor="exampleCheck1">Check me out</label>
                    </div> */}
                    <button type="submit" className="btn btn-primary">Submit</button>
                </form>
                :<div className='sub-done' >
                    Submited Done... Thank You
                </div> }
                

            </React.Fragment>
        );
    }
}
 
export default Login;